.world-cup {
  display: flex;
  align-items: center;
  padding: 40px 8%;
  justify-content: space-between;
  background-image: url(../../Images/ImageOne.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  @media (max-width: 1024px) {
    padding: 10px;
  }
  @media (max-width: 768px) {
    flex-direction: column;
    padding: 10px;
  }
  .wc-text {
    // position: relative;
    padding: 10px;
    width: 32%;
    z-index: 2;
    @media (max-width: 1024px) {
      width: 45%;
    }
    @media (max-width: 768px) {
      width: 100%;
      padding: 20px;
      text-align: center;
    }
    h1 {
      font-size: 32px;
      //   font-size: 2.566rem;
      line-height: 1;
      line-height: 100%;
      margin-bottom: 20px;
      color: #343b92;
    }
    p {
      margin-bottom: 20px;
    }
  }
  .slide-card {
    // position: relative;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
    z-index: 2;
    @media (max-width: 558px) {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    .slide {
      width: 390px;
      box-shadow: #343a929f 1px 1px 10px 1px;
      border-bottom: 4px solid #fbff00;
      background-color: #343b92;
      @media (max-width: 1024px) {
        width: 300px;
      }
      @media (max-width: 768px) {
        width: 100%;
      }
      img {
        max-width: 100%;
        height: auto;
      }
      .slide-text {
        margin-top: -10px;

        padding: 10px;
        color: white;
        h5,
        p {
          font-size: 14px;
          font-style: italic;
          font-weight: 300;
          span {
            margin-left: 5px;
          }
        }
        h1 {
          font-weight: 400;
          margin: 20px 0;
          font-size: 16px;
        }
      }
    }
  }
}
