.ranking {
  width: 100%;
  padding: 40px 8%;
  display: flex;
  align-items: center;
  background-color: #343b92;
  @media (max-width: 1024px) {
    padding: 10px;
    flex-direction: column;
  }
  .rank-text {
    // position: relative;
    padding: 10px;
    width: 32%;
    z-index: 2;
    margin-right: 50px;
    @media (max-width: 1024px) {
      width: 100%;
      padding: 20px;
      text-align: center;
    }
    h1 {
      font-size: 32px;
      text-transform: uppercase;
      line-height: 1;
      line-height: 100%;
      margin-bottom: 20px;
      color: #ffffff;
    }
    p {
      margin-bottom: 20px;
    }
  }
  .rank {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 680px) {
      flex-direction: column;
    }
    .ranks {
      width: 300px;
      margin: 20px;
      padding: 20px;
      min-height: 300px;
      border-radius: 10px;
      box-shadow: 2px 2px 10px 2px #06216b;
      @media (max-width: 680px) {
       width: 90%px;
      }
      h1 {
        font-size: 22px;
        text-transform: uppercase;
        line-height: 1;
        line-height: 100%;
        margin-bottom: 20px;
        color: #ffffff;
      }
      img {
        width: 100%;
        height: auto;
        object-fit: cover;
      }
    }
  }
}
