.comp-news {
  width: 100%;
  padding: 0 100px;
  padding-bottom: 100px;
  @media (max-width: 1024px) {
    padding: 10px;
  }
  h1 {
    font-size: 32px;
    //   font-size: 2.566rem;
    line-height: 1;
    line-height: 100%;
    margin: 20px 0;
    color: #343a92;
    text-transform: uppercase;
    @media (max-width: 768px) {
      font-size: 100%;
    }
  }
  hr {
    margin: 20px 0;
    width: 200px;
    border: #343b92 solid 4px;
  }
  .stories {
    // position: relative;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    z-index: 2;
    min-height: 200px;
    @media (max-width: 1024px) {
    }
    @media (max-width: 768px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 468px) {
      grid-template-columns: repeat(1, 1fr);
    }
    .post {
      width: 340px;
      box-shadow: #343a929f 1px 1px 10px 1px;
      height: 100%;
      background-color: #343b92;
      border-bottom: 4px solid gold;
      @media (max-width: 1024px) {
        width: 100%;
      }
      img {
        max-width: 100%;
        height: auto;
      }
      .post-text {
        margin-top: -10px;
        //

        padding: 10px;

        h1 {
          font-weight: 400;
          margin: 20px 0;
          font-size: 16px;
          color: white;
          text-transform: capitalize;
          text-align: justify;
        }
        .btn-more {
          width: 100px;
          position: relative;
          //   right: 0;
        }
      }
    }
  }
}
