* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: poppins;
  font-weight: 300;
  font-size: 100%;
  font-weight: 500;
}

.btn-more {
  padding: 10px;
  width: 200px;
  background-color: white;
  border-bottom: 2px solid #343b92;
  color: #343b92;
  clip-path: polygon(0 0%, 90% 0%, 100% 100%, 0% 100%);
  border: 0;
  cursor: pointer;
  &:hover {
    color: white;
    background-color: #343b92;
    border: 1px solid white;
  }
}
.btn-scroll {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  color: #343b92;
  background-color: gold;
  position: absolute;
  right: 10px;
  bottom: 10px;
}

.about-menu {
  width: 100%;
  min-height: 500px;
  padding: 5%;
  background-color: #ffffff;
  // display: none;
  color: #343b92;
  display: none;
  @media (max-width: 768px) {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .abt-nav {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-row-gap: 10px;
    grid-column-gap: 30px;
    @media (max-width: 608px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 408px) {
      grid-template-columns: repeat(1, 1fr);
    }
    Link {
      padding: 10px;
      .right {
        margin-right: 10px;
      }
    }
  }
}
