.slider-wrapper {
  width: 100%;
  height: auto;
  @media (max-width: 990px) {
    background-color: red;
    margin-top: 60px;
  }
  //   background-color: blue;
  position: relative;
  .arrows {
    width: 100%;
    position: absolute;
    top: calc(50% - 100px);
    @media (max-width: 600px) {
      top: 20%;
    }
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 99;
    .arrow {
      background-color: rgba(0, 0, 255, 0.281);
      padding: 10px;
      font-size: 0.2rem;
      color: white;
      width: 60px;
      height: 120px;
      cursor: pointer;
      @media(max-width:990ps){
        width: 45px;
        height: 90px;
      }
      @media (max-width: 768px) {
        width: 30px;
        height: 60px;
        padding: 0;
        font-size: 100%;
      }
      &:hover {
        background-color: rgba(0, 0, 255, 0.739);
      }
    }
  }

  .slide {
    width: 100%;
    .slides {
      width: 100%;
      min-height: 400px;
      box-shadow: #343b92 1px 1px 10px 1px;
      overflow: hidden;
      position: relative;
      img {
        position: relative;
        max-width: 100%;
        height: auto;
        width: 100%;
        object-fit: cover;
        animation: pulse;
        animation-duration: 4.83s;
      }
      .slides-text {
        width: 800px;
        padding: 20px 100px;
        position: absolute;
        bottom: 10%;
        left: 10%;
        @media(max-width:990px){
          width: 65%;
          padding: 10px;
          clip-path: none;
          bottom: 40%;
        }
        background-color: rgba(255, 255, 255, 0.459);
        border-bottom: 4px solid #343b92;
        backdrop-filter: blur(1em);
        clip-path: polygon(10% 0%, 100% 0%, 90% 100%, 0% 100%);
        h1 {
          color: #343b92;
          margin: 10px 0;
          @media(max-width:990px){
            font-size:12px;
          }
        }
        .btn-more {
          padding: 10px;
          width: 200px;
          background-color: white;
          border-bottom: 2px solid #343b92;
          color: #343b92;
          clip-path: polygon(0 0%, 90% 0%, 100% 100%, 0% 100%);
          border: 0;
          cursor: pointer;
          @media(max-width:990px){
            width: 40%;
            font-size: 12px;
            padding: 5px;
          }
          &:hover {
            color: white;
            background-color: #343b92;
          }
        }
      }
    }
  }
}
