.footer {
  padding: 0;
  background-color: #000;
  .partners {
    padding: 20px 8%;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    @media (max-width: 1024px) {
      padding: 10px;
    }

    h1 {
      font-size: 38px;
      text-transform: uppercase;
      line-height: 1;
      line-height: 100%;
      margin-bottom: 20px;
      color: #343b92;
    }
    .logos {
      display: flex;
      align-items: center;
      justify-content: space-around;
      flex-wrap: wrap;
      img {
        width: 100px;
        margin: 20px;
      }
    }
  }
  .bfooter {
    width: 100%;
    padding: 20px 8%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 1024px) {
      padding: 10px;
    }
    .footlink {
      color: #fff;
      Link {
        color: #fff;
      }
    }
  }
}
