.cranes {
  padding: 40px 8%;
  width: 100%;
  @media (max-width: 1024px) {
    padding: 10px;
  }
  .crane {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 768px) {
      flex-direction: column;
      padding: 10px;
    }
    .crane-text {
      // position: relative;
      padding: 10px;
      width: 32%;
      z-index: 2;
      @media (max-width: 768px) {
        width: 100%;
        padding: 20px;
        text-align: center;
      }
      h1 {
        font-size: 32px;
        text-transform: uppercase;
        line-height: 1;
        line-height: 100%;
        margin-bottom: 20px;
        color: #343b92;
      }
      p {
        margin-bottom: 20px;
      }
    }
    .players {
      display: flex;
      align-items: center;
      justify-content: space-between;
      @media (max-width: 608px) {
        // flex-direction: column;
      }
      .player-card {
        width: 200px;
        border-radius: 10px;
        box-shadow: 2px 2px 10px 2px #a7a7a7;
        background-color: #343b92;
        padding: 0 20px;
        overflow: hidden;
        @media (max-width: 608px) {
          width: 33%;
          padding: 10px;
          text-align: center;
        }
        img {
          width: 100%;
        }
        .player-text {
          text-align: center;

          background-color: #fff;
          h1 {
            padding: 10px;
            //   box-shadow: 2px 2px 10px 2px #06216b;
            span {
              padding: 5px;
              background-color: gold;
              @media (max-width: 608px) {
                padding: 0;
              }
            }
          }
          p {
            padding: 5px;
            background-color: #343b92;
          }
        }
      }
    }
  }
  //   news
  .cranes-news {
    padding: 40px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .wlist {
      // position: relative;
      padding: 10px;
      width: 32%;
      z-index: 2;
      h1 {
        font-size: 32px;
        text-transform: uppercase;
        line-height: 1;
        line-height: 100%;
        margin-bottom: 20px;
        color: #343b92;
      }
      p {
        margin-bottom: 20px;
      }
    }
    .slide-card {
      // position: relative;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 15px;
      z-index: 2;
      @media (max-width: 1024px) {
        gap: 15px;
      }
      @media (max-width: 768px) {
        // display: grid;
        grid-template-columns: repeat(3, 1fr);
      }
      @media (max-width: 608px) {
        // display: grid;
        grid-template-columns: repeat(2, 1fr);
      }
      @media (max-width: 340px) {
        // display: grid;
        grid-template-columns: repeat(1, 1fr);
      }
      .slide {
        width: 350px;
        box-shadow: #343a929f 1px 1px 10px 1px;
        border-bottom: 4px solid #fbff00;
        background-color: #343b92;
        @media (max-width: 1024px) {
          width: 310px;
        }
        @media (max-width: 768px) {
          width: 100%;
        }
        img {
          max-width: 100%;
          height: auto;
        }
        .slide-text {
          margin-top: -10px;

          padding: 10px;
          color: white;
          h5,
          p {
            font-size: 14px;
            font-style: italic;
            font-weight: 300;
            span {
              margin-left: 5px;
            }
          }
          h1 {
            font-weight: 300;
            margin: 20px 0;
            font-size: 16px;
            color: white;
            text-transform: capitalize;
          }
        }
      }
    }
  }
}
