.women {
  padding: 40px 8%;
  background-color: #e1e1e1;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: space-between;
  @media (max-width: 1024px) {
    flex-direction: column;
    padding: 10px;
  }
  .women-text {
    // position: relative;
    padding: 10px;
    width: 32%;
    z-index: 2;
    @media (max-width: 1024px) {
      width: 100%;
      text-align: center;
    }
    h1 {
      font-size: 32px;
      text-transform: uppercase;
      line-height: 1;
      line-height: 100%;
      margin-bottom: 20px;
      color: #343b92;
    }
    p {
      margin-bottom: 20px;
    }
  }
  .slide-card {
    // position: relative;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
    z-index: 2;
    @media (max-width: 768px) {
      gap: 10px;
    }
    @media (max-width: 608px) {
      grid-template-columns: repeat(1, 1fr);
    }
    .slide {
      width: 100%;
      box-shadow: #343a929f 1px 1px 10px 1px;
      border-bottom: 4px solid #fbff00;
      background-color: #343b92;
      @media (max-width: 768px) {
        // display: grid;
        grid-template-columns: repeat(3, 1fr);
      }
      @media (max-width: 608px) {
        // display: grid;
        grid-template-columns: repeat(2, 1fr);
      }
      img {
        max-width: 100%;
        height: auto;
      }
      .slide-text {
        margin-top: -10px;

        padding: 10px;
        color: white;
        h5,
        p {
          font-size: 14px;
          font-style: italic;
          font-weight: 300;
          span {
            margin-left: 5px;
          }
        }
        h1 {
          font-weight: 400;
          margin: 20px 0;
          font-size: 16px;
        }
      }
    }
  }
}
