.shoping {
  width: 100%;
  padding: 40px 100px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-image: url(../../Images/trt.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  @media (max-width: 1024px) {
    padding: 10px;
    flex-direction: column;
  }
  .shop-text {
    // position: relative;
    padding: 10px;
    width: 32%;
    z-index: 2;
    @media (max-width: 768px) {
      width: 100%;
      text-align: center;
    }
    h1 {
      font-size: 32px;
      text-transform: uppercase;
      line-height: 1;
      //   line-height: 100%;
      margin-bottom: 20px;
      color: #343b92;
    }
    p {
      margin-bottom: 20px;
    }
  }
  .jersey {
    width: 700px;
    @media (max-width: 768px) {
      width: 100%;
    }

    img {
      width: 100%;
    }
  }
}
