.navbar {
  width: 100%;
  padding: 0 5%;
  height: 60px;
  position: fixed;
  top: 0;
  background-color: #343b92;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 999;
  border-bottom: 4px solid rgb(255, 255, 255);
  @media (max-width: 1024px) {
    background-color: red;
    margin-bottom: 60px;
    padding: 10px;
  }
  @media (max-width: 768px) {
    padding: 10px;
  }

  //   navbar navigatio n menu
  .navbar-nav {
    width: 75%;
    display: flex;
    align-items: center;
    list-style: none;
    justify-content: space-between;
    @media (max-width: 768px) {
      width: 100%;
      // height: 100%;
      position: absolute;
      top: 0;
      padding: 20px;
      text-align: left;
      flex-direction: column;
      background-color: #343b92;
      overflow: auto;
      z-index: 999;
    }
    // Nvavbar menu item/link
    .nav-link {
      height: 60px;
      text-decoration: none;
      display: flex;
      align-items: center;
      padding: 0 20px;
      // border-right: 2px solid white;

      @media (max-width: 1024px) {
      }
      @media (max-width: 768px) {
        clip-path: none;
        padding: 10px;
        width: 100%;
      }
      // position: relative;
      color: white;
      &:hover {
        background-color: #fff;
        color: #343b92;
        .mega {
          position: absolute;
          display: block;
          top: 61px;
          @media (max-width: 768px) {
            display: none;
          }
        }
      }
      &:focus {
        background-color: #fff;
        color: #343b92;
      }
      //   icon{
      .link-icon {
        margin: 0 5px;
        @media (max-width: 1024px) {
          display: none;
        }
      }
      //   link text{
      .link-text {
        text-transform: uppercase;
        font-size: 16px;
      }

      // mega element
      .mega {
        position: absolute;
        top: -5px;
        left: 0;
        width: 100%;
        display: none;
        min-height: 500px;
        // padding: 5%;
        // background-color: #343b92;
        cursor: default;

        // color: white;
        z-index: 999;
        .about-menu {
          width: 100%;
          min-height: 500px;
          padding: 5%;
          background-color: #ffffff;
          // display: none;
          color: #343b92;
          display: flex;
          align-items: center;
          justify-content: space-between;
          .abt-nav {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            grid-row-gap: 10px;
            grid-column-gap: 30px;

            Link {
              padding: 10px;
              .right {
                margin-right: 10px;
              }
            }
          }
        }
      }
    }
    // logo
  }
  .logo {
    margin: 10px 20px;
    text-decoration: none;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
    h1 {
      font-size: 2em;
      text-transform: uppercase;
      font-weight: bold;
    }
    img {
      width: 70px;
      margin-top: 40px;
    }
  }
  .toggler {
    display: none;
    color: #fff;
    font-size: 22px;
    z-index: 1000;
    @media (max-width: 768px) {
      display: block;
    }
  }
}
